import { request } from './request';
export function addComment(blogId, username, email, resaveEmail, URL, comment, replyId, replyCommentId, replyUsername, isRegisted, commentAvatar) {
  return request({
    url: '/comment/new',
    method: 'post',
    data: {
      blogId: blogId,
      username: username,
      email: email,
      resaveEmail: resaveEmail,
      URL: URL,
      comment: comment,
      replyId: replyId,
      replyCommentId: replyCommentId,
      replyUsername: replyUsername,
      isRegisted: isRegisted,
      commentAvatar: commentAvatar
    }
  });
}
export function getCommentList(id) {
  return request({
    url: '/comment/getlist',
    method: 'get',
    params: {
      id: id
    }
  });
} //通过审核和删除评论

export function controlComment(passlist, deleteList) {
  return request({
    method: 'post',
    url: '/comment/controlComment',
    data: {
      passlist: passlist,
      deleteList: deleteList
    }
  });
}