import CommentList from "components/common/comment/CommentList";
export default {
  name: "CommentVerify",
  components: {
    CommentList: CommentList
  },
  created: function created() {
    var _this = this;

    getLoginCheck().then(function (res) {
      if (res.data.isAdmin === true) {
        _this.text = "审查和删除评论";
        _this.admin = true;
      } else {
        _this.text = '没有权限✖';
      }
    });
  },
  data: function data() {
    return {
      text: "验证权限中...",
      admin: false,
      isShowList: [],
      deleteList: []
    };
  },
  methods: {
    deleteComment: function deleteComment(item) {
      this.deleteList.push(item);
    },
    addList: function addList(item) {
      this.isShowList.push(item);
    },
    submit: function submit() {
      controlComment(this.isShowList, this.deleteList).then(function (res) {// console.log(res);
      });
    }
  }
};